import React from 'react';

import { Card, Typography, Timeline } from 'antd';
import { HistoryOutlined } from '@ant-design/icons';
import { Player } from '../types';
const { Text } = Typography;

interface Transaction {
  type: string;
  players: Player[];
  data: {
    fromId?: number;
    toId?: number;
    name?: string;
    balance?: number;
    amount?: number;
    id?: number;
  };
}

/**
 *  .
 * @param {Transaction[]} events -  
 * @param {Player[]} players -  
 * @component TransactionsHistory
 */
const TransactionsHistory: React.FC<{ events: Transaction[]; players: Player[] }> = ({ events, players }) => {
  if (!events || events.length === 0) return null;

  return (
    <Card
      title={
        <>
          <HistoryOutlined />  
        </>
      }
      className="history-card"
    >
      <Timeline
        items={events.slice(0, 10).map((entry) => {
          //  
          const isAdd = entry.type === 'ADD_PLAYER';
          //   -  
          const isEliminated = entry.type === 'ELIMINATED';
          //  
          const resolveName = (id: number) => {
            const p = players.find((player) => player.id === id);
            return p?.name ?? ` ${id}`;
          };
          const fromName = resolveName(entry.data.fromId);

          const toName = players.find((p) => p.id === entry.data.toId)?.name ?? ` ${entry.data.toId ?? ''}`;
          const elimName = `${entry.data ?? ''}`;

          return {
            color: isAdd ? 'green' : isEliminated ? 'red' : 'blue',
            children: (
              <div>
                {isEliminated ? (
                  <Text>{elimName}   </Text>
                ) : isAdd ? (
                  <Text>
                    : {entry.data.name}, : {(entry.data.balance ?? 0) / 1_000_000}  .
                  </Text>
                ) : (
                  <>
                    <Text>{fromName}</Text>
                    <Text>   </Text>
                    <Text>{toName}</Text>
                    <Text> : {(entry.data.amount ?? 0) / 1_000_000}  .</Text>
                  </>
                )}
                <br />
                <Text type="secondary" style={{ fontSize: '12px' }}>
                  {new Date().toLocaleString()}
                </Text>
              </div>
            ),
          };
        })}
      />
    </Card>
  );
};

export default TransactionsHistory;
